<template>
  <Card class="shadow-lg h-full">
    <template #header>
      <CardHeader title="Affiliates" date="February, 10 2020" />
    </template>
    <template v-if="getFormatted.items.length != 0">
      <div v-for="(affiliate, index) in getFormatted.items" class="d-flex justify-content-between align-items-center" :class="{ 'mb-3': index + 1 != getFormatted.items.length }" :key="index">
        <div class="d-flex flex-row align-items-center">
          <b class="text-dark mr-3">{{ index + 1 }}</b>
          <div>
            <b class="d-block text-dark">{{ affiliate.affiliation.first_name }} {{ affiliate.affiliation.last_name }}</b>
            <small>{{ affiliate.affiliation.email }}</small>
          </div>
        </div>
        <b class="text-dark">
          <Currency :value="parseFloat(affiliate.total).toFixed(2).toLocaleString()" />
        </b>
      </div>
    </template>
    <div v-else class="text-center">
      <p class="font-bold text-red mb-0">Nothing to see here</p>
    </div>
  </Card>
</template>

<script>
import { defineComponent } from 'vue'
import { useAffiliates } from 'composable/dashboard'
import Card from 'bbUi/components/card/card.vue'
import CardHeader from 'components/dashboard/CardHeader.vue'
import Currency from 'components/Currency.vue'


export default defineComponent({
  components: {
    Card,
    CardHeader,
    Currency
  },
  async setup() {
    const { data, getFormatted, affiliates } = useAffiliates()
    
    await affiliates()

    return {
      data,
      getFormatted
    }
  }
})
</script>